<template>
  <div
    class="page-about"
    :class="{ 'is-popup-showing': isPopupShowing }"
  >
    <transition
      name="transition-header"
      :duration="{ enter: 2000, leave: 600 }"
    >
      <div
        v-if="isPopupShowing"
        class="page-news__header"
      >
        <the-header-inner/>
      </div>
    </transition>

    <div class="page-about__root">
      <div id="page-about__wrapp" ref="contentWrap" class="page-post__wrapp">
        <the-header-new />

        <div class="page-about__main">
          <div class="page-about__main-wrapper">
            <div class="page-about__main-content">
              <div class="page-about__main-sidebar">
                <div class="page-about__sidebar-post">
                  <router-link :to="{ name: 'aboutus' }" :class="{ 'router-link-exact-active': ['aboutus'].includes($route.name) }" class="page-about__sidebar-post-title">About us</router-link>
                </div>
                <div class="page-about__sidebar-post">
                  <router-link :to="{ name: 'aboutethermium' }" class="page-about__sidebar-post-title">What is DMEX?</router-link>
                </div>
                <div class="page-about__sidebar-post">
                  <router-link :to="{ name: 'aboutdecenralization' }" class="page-about__sidebar-post-title">What is Decentralization?</router-link>
                </div>
                <div class="page-about__sidebar-post">
                  <router-link :to="{ name: 'support' }" class="page-about__sidebar-post-title">Contact us</router-link>
                </div>
                <div class="page-fees__sidebar-post">
                  <router-link :to="{ name: 'fees' }" class="page-fees__sidebar-post-title">Fees</router-link>
                </div>
              </div>

              <div class="page-about__main-article">
                <div class="page-about__heading">
                  <div @click="$router.go(-1)" class="page-about__back"></div>
                  <div class="page-about__heading-main">About us</div>
                  <div class="page-about__heading-description">We are a team of engineers and designers working to create the future of trading.</div>
                </div>

                <div class="page-about__text">
                  <p>We firmly believe in free trade and in the sovereignty of the individual to make his/her own decisions. We think that trade is a critical driver of our civilization and any restrictions on trade are hindering our progress.</p>
                  <p>The blockchain is a gift for individuals that believe in freedom. It allows for the creation of systems devoid of trust in third parties — systems that always work as expected, without any unnecessary delays or third-party interventions.</p>
                  <p>We have embarked on this path being convinced that this technology is the future and any future devoid of freedom is just inconceivable. Our vision and views are fully represented in our product.</p>
                  <p>We believe in business and our loyalty is only to our customers. Not to governments or any other party.</p>
                </div>
              </div>
            </div>
          </div>
        </div>

        <the-footer/>
      </div>
    </div>

    <!-- popup and menu -->
    <the-popup v-if="isPopupShowing"/>
    <the-menu-sidebar v-if="isPopupShowing"/>
    <!-- popup and menu -->
  </div>
</template>

<script>
/**
 * Component: Страница поста
 *
 * @version 2.0.0
 */


import { mapGetters } from 'vuex';

import iconArrow from '@/icons/e-icon_arrow.svg';
import iconBack from '@/icons/e-icon__back-big.svg';

// Components
import TheTokenSearchEtherscanMobile from '@/containers/blocks/TheTokenSearchEtherscanMobile';
import EBigNumber from '@/components/EBigNumber';
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';
import TheFooter from '@/containers/blocks/TheFooter';
import TheHeaderInner from '@/containers/blocks/TheHeaderInner';
import TheHeaderNew from '@/containers/blocks/TheHeaderNew';
import TheMenuSidebar from '@/containers/blocks/TheMenuSidebar';
import ThePopup from '@/containers/blocks/ThePopup';
import TheMobileTokenSearch from '@/containers/blocks/TheMobileTokenSearch';
import TheTokenSearchResultNew from '@/containers/blocks/TheTokenSearchResultNew';


export default {
  name: 'page-aboutus',

  data() {
    return {
      iconArrow,
      iconBack,
    };
  },

  computed: {
    ...mapGetters({
      isPopupShowing: 'ui/isPopupShowing',
    }),
  },

  async mounted() {
    this.$ga.page(this.$router);
  },

  components: {
    TheTokenSearchEtherscanMobile,
    TheMobileTokenSearch,
    TheTokenSearchResultNew,
    EBigNumber,
    EButton,
    EIcon,
    TheFooter,
    TheHeaderInner,
    TheHeaderNew,
    TheMenuSidebar,
    ThePopup,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.page-about {
  $this: &;

  &__root {
    font-weight: 500;
    background-color: #f2f2f2;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    overflow-y: auto;
  }

  &__wrapp {
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
    height: 100%;
  }

  &__header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 78px;
    z-index: 102;
  }

  // MAIN section
  // ==================================================
  &__main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #031472;
    position: relative;
    margin-top: -120px;
    padding-top: 120px;
    z-index: 0;

    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      z-index: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(to bottom, rgba(5,4,26,.5) 0%,rgba(5,4,26,.4) 30%, rgba(5,4,26,0) 70%, rgba(5,4,26,0) 100%);
    }

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      transform: translate(-50%, 0);
      z-index: 0;
      bottom: -120px;
      width: 100%;
      max-width: 1600px;
      height: 180px;
      background: #030C52;
      border-radius: 50%;
      filter: blur(30px);
      opacity: .4;

      @include r(992) {
        display: none;
      }
    }
  }

  &__main-wrapper {
    background: #f2f2f2;
    z-index: 1;
  }

  &__main-content {
    width: 100%;
    max-width: 1440px;
    padding: 70px 130px 190px;
    box-sizing: border-box;
    margin: 0 auto;
    position: relative;
    display: flex;
    justify-content: space-between;

    @include r(1200) {
      padding: 70px 90px 150px;
    }

    @include r(992) {
      position: relative;
      padding: 50px 40px;
      flex-direction: column-reverse;
      justify-content: flex-start;
    }
  }

  &__main-article {
    width: 75%;
    margin-right: 120px;
    position: relative;

    @include r(1220) {
      margin-right: 60px;
    }

    @include r(992) {
      width: 100%;
    }

    @include r(420) {
      margin-right: 0;
      margin-bottom: 40px;
    }
  }

  &__heading {
    font-weight: normal;
    margin-bottom: 32px;
    position: relative;

    @include r(640) {
      margin-bottom: 26px;
    }
  }

  &__back {
    position: absolute;
    display: inline-block;
    width: 17px;
    height: 24px;
    cursor: pointer;
    left: -47px;
    top: 16px;

    @include r(768) {
      left: -32px;
    }

    @include r(640) {
      top: 11px;
    }

    &::before, &::after {
      display: block;
      position: absolute;
      content: '';
      width: 17px;
      height: 2px;
      background: #1f2ee9;
    }

    &::before {
      transform: rotate(-45deg);
      left: 0;
      top: 5px;
    }

    &::after {
      transform: rotate(45deg);
      left: 0;
      top: 16px;
    }
  }

  &__heading-main {
    color: #0d105b;
    font-size: 34px;
    font-weight: normal;
    margin-bottom: 20px;

    @include r(640) {
      line-height: 43px;
    }
  }

  &__heading-description {
    color: #000;
    font-size: 23px;
    line-height: 36px;
  }

  &__text {
    p {
      color: #000;
      opacity: 0.6;
      font-size: 16px;
      line-height: 24px;
      font-weight: normal;
    }
  }

  &__main-sidebar {
    padding-top: 19px;
    width: 25%;

    @include r(992) {
      position: static;
      transform: translateX(0);
      box-shadow: none;
      width: 100%;
      padding-top: 70px 0 0;
      background: transparent;
      height: auto;
    }
  }

  &__sidebar-post {
    margin-bottom: 16px;
  }

  &__sidebar-post-title {
    font-size: 16px;
    color: #1F2EE9;
    line-height: 21px;
    letter-spacing: 0.5px;
    transition: all 0.2s;
    cursor: pointer;
    margin-top: 5px;
    text-decoration: none;

    &.router-link-exact-active {
      color: #000;
    }

    &:hover {
      color: #000;
    }
  }
}

</style>



